import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import HomePage from './components/homepage/homepage';

   function  App ()  {
      return(
          <Router>
            <div style={{ display: 'flex' }}>
              <Switch>
                <Route exact path="/">
                  <HomePage  />
                </Route>
              </Switch>
            </div>
          </Router>
      )
   } 

   export default App;